@import url(https://fonts.googleapis.com/css?family=Josefin+Sans);
.Browse {
  display: grid;
  color: #fff;
  grid-template-columns: 1fr 1fr;
}

.Browse p {
  color: #fff;
  font-size: 3em;
  grid-column-start: 1;
  grid-column-end: 3;
  margin-top: 2em;
  text-align: center;
  width: 100%;
}

h3 {
  padding-top: 2em;
  font-size: 1.5em;
  color: white;
}

.Create {
  display: grid;
  grid-template-columns: 1fr 4fr;
  position: relative;
  width: 100vw;
}

.Create form {
  background-color: #000;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
}

.Create textarea {
  margin-top: 4em;
}

.hubble-img {
  height: 100%;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
  position: relative;
}

.backward-btn,
.forward-btn {
  background: transparent;
  border: none;
  height: 100%;
  width: 50%;
  position: absolute;
}

.backward-btn:focus,
.forward-btn:focus {
  outline: 0;
}

.backward-btn {
  left: 0;
  text-align: left;
}

.forward-btn {
  right: 0;
  text-align: right;
}

.InfoPage {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr;
  color: #fff;
  width: 100vw;
  height: 100vh;
  background-color: #000;
}

.scroll-to-home {
  background-color: transparent;
  border: none;
  position: absolute;
  top: 2em;
}

.scroll-to-home:focus {
  outline: 0;
}

.Info-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Info-header {
  font-size: 7em;
  font-family: 'Josefin Sans', sans-serif;
  margin: 5vh 0 0;
  text-align: center;
  width: 100%;
}

.Info-text {
  font-size: 1.5em;
  margin-left: 30px;
  text-indent: 1.5em;
  width: 70%;
}

img {
  padding-top: 35px;
  width: 400px;
}

.Home {
  position: relative;
}

.NavLink-create {
  margin: 0 auto;
  color: #fff;
  display: flex;
  flex-direction: column;
  position: fixed;
  text-decoration: none;
  top: 0;
  right: 0;
  z-index: 999;
}

.Home-title-page {
  background-image: url('http://imgsrc.hubblesite.org/hvi/uploads/image_file/image_attachment/28735/xlarge_web.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  width: 100vw;
}

.title-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100vh;
  width: 100vw;
}

.title-box {
  background-color: rgba(0, 0, 0, 0.7);
  margin: 0 auto;
  padding: 20px;
  width: 80%;
}

.Home-title {
  border-bottom: 7px solid #fff;
  color: #fff;
  margin: 0 auto;
  font-size: 8em;
  font-family: 'Josefin Sans', sans-serif;
}

.Home-subheader {
  color: #fff;
  margin: 15px auto;
  font-size: 3em;
  font-family: 'Josefin Sans', sans-serif;
}

.button-header {
  margin: 0 auto;
  color: #fff;
  font-size: 3em;
  font-family: 'Josefin Sans', sans-serif;
}

.button-wrapper {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  bottom: 100vh;
  width: 100%;
  margin: 0 auto;
}
.scroll-to-info {
  border: none;
  background: transparent;
  width: 15%;
}

.scroll-to-info:focus {
  outline: 0;
}

i {
  height: 65px;
  color: #fff;
  font-size: 80px;
}

.Nav {
  background-color: #000;
  display: flex;
  position: fixed;
  top: 0;
  width: 100vw;
  z-index: 999;
}

.NavLink {
  color: #fff;
  font-size: 2em;
  margin-left: 1em;
  text-decoration: none;
}

.NavLink:hover {
  text-decoration: underline;
}

.active {
  text-decoration: underline;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

body {
  background-color: #000;
  margin: 0;
  padding: 0;
}

